.home_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: var(--color-black);
  height: 100vh;
  width: 100%;
  padding-top: 100px;
  padding-bottom: 50px;
  border-bottom: solid 1px var(--color-blue);
}

.home_container img {
  width: 500px;
  max-width: 80%;
}

.menu_container {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.buttons_container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-top: 30px;
}

@media screen and (max-width: 600px) {
  .home_container {
    width: 100%;
  }

  .buttons_container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
