.contact_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
    width: 100%;
    border-bottom: solid 1px var(--color-blue);
}

@media screen and (max-width: 1010px) {

    .contact_container h1{
        padding-bottom: 100px;
    }

    #contact_card{
        margin-bottom:50px;
    }
}


@media screen and (max-width: 600px) {

    .contact_container{
        min-height: inherit;
        height: 70vh;
    }

    .card_container ul li{
        font-size: 14px;
    }

    .card_container ul li a{
        font-size: 8px;
        font-style: inherit;
    }
}